.title {
  text-align: center;
  font-size: 30px;
  text-transform: uppercase;
}
.tour-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.single-tour {
  width: 400px;
  background: white;
  border-radius: 10px;
  margin: 2rem;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s linear;
}
.single-tour:hover {
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}
.single-tour img {
  width: 100%;
  height: 20rem;
  opacity: 0.8;
  filter: drop-shadow(2px 2px 5px black);
  object-fit: cover;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info-title {
  text-align: center;

  text-transform: uppercase;
  font-size: 20px;
}

.info-body {
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 20px;
}
.read-more {
  color: blue;
  font-size: 20px;
  border-color: transparent;
  cursor: pointer;
}

.delete {
  background: green;
  display: inline-block;
  padding: 0.25rem 0.5rem;
  border-radius: 10px;
  text-transform: capitalize;
  color: white;
  letter-spacing: 0.1rem;
  border-color: transparent;
  cursor: pointer;
  font-size: 1.2rem;
  margin-bottom: 20px;
}
